aside {
  display: flex;
  font-size: 12px;
  width: 100%;
}

aside .tab {
  display: flex;
  flex-direction: column;
  width: 4%;
  height: 100%;
  background-color: #4e5058;
}

aside .tab_image {
  width: 40%;
  margin-left: 18%;
  margin-top: 30%;
}
aside .yolo_image {
  width: 49%;
  margin-left: 25%;
  margin-top: 2%;
}

aside .selected .tab_image {
  margin-left: 18%;
  margin-top: 30%;
  background-color: lightgrey;
  padding: 15% 10%;
  border-radius: 40% 0 0 40%;
}

aside .main {
  width: 100%;
  display: flex;
  flex-direction: row;
}
